.hero {
    height: 100vh;
    position: relative;
    color: #fff;
  }
  .hero .hero-content {
    text-align: center;
    position: absolute;
  }
  .hero .overlay-itro {
    background-color: #393E46;
    position: absolute;
    top: 0;
    left: 0px;
    padding: 0;
    height: 100%;
    width: 100%;
    opacity: 0.9;
  }
  .hero .hero-title {
    color: #fff;
    font-weight: 600;
    font-size: 3rem;
  }
  @media (min-width: 768px) {
    .hero .hero-title {
      font-size: 4.5rem;
    }
  }
  .hero .hero-subtitle {
    font-size: 1.5rem;
    font-weight: 600;
  }
  @media (min-width: 768px) {
    .hero .hero-subtitle {
      font-size: 2.5rem;
    }
  }
  .hero .text-slider-items {
    display: none;
  }
  
  .hero-single {
    height: 350px;
  }
  .hero-single .hero-content {
    margin-top: 30px;
  }
  .hero-single .hero-title {
    text-transform: uppercase;
    font-size: 3rem;
  }
  @media (min-width: 768px) {
    .hero-single .hero-title {
      font-size: 3.5rem;
    }
  }
  .hero-single .breadcrumb {
    background-color: transparent;
    color: #0078ff;
  }
  .hero-single .breadcrumb .breadcrumb-item:before {
    color: #cde1f8;
  }
  .hero-single .breadcrumb .breadcrumb-item.active {
    color: #cde1f8;
  }
  .hero-single .breadcrumb a {
    color: #fff;
  }

  .c1{
    display: inline-block;
  }
  .c2{
    display: inline-block;
  }
  .c3{
    display: inline-block;
  }
  h1 .type{
    font-size: 2.5rem;
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap; 
    width: 0;
    animation: 
      typing 1s steps(25, end) forwards;
}
h1 .type2{
    font-size: 2.5rem; 
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap; 
    width: 0;
    animation: 
      typing 1s steps(25, end) forwards;
    animation-delay: 1s;
}
h1 .type3{
    font-size: 2.5rem; 
    overflow: hidden;
    border-right: .15em solid orange; 
    white-space: nowrap; 
    width: 0;
    animation: 
      typing 1s steps(25, end) forwards;
    animation-delay: 2s;
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes blink {
  from { border-color: transparent }
  to { border-color: orange; }
}