#education{
    background-color: #fff;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 5px;;
    border-radius: 50px;
}

.heading{
    font-size: 30px;
    font-weight: bold;
    color: black;
    margin-left: 30px;
    padding-bottom: 15px;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

#resume-section{
    padding-top: 30px; 
    padding-left:  30px;
    padding-bottom: 25px;
    padding-right: 30px;
    background-color: #ADD8E6;
    border: 20px;
}

@media (min-width: 576px) {
    .container {
        max-width: 540px;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

.row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.page {
    width: 100%;
    margin-bottom: 2em;
}

.page.four {
    margin-bottom: 0;
}

.page .heading {
    font-weight: 800;
    font-size: 30px;
    margin-bottom: 30px;
    color: #3e64ff;
}

.resume-wrap {
    width: 100%;
    margin-bottom: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-bottom: 10px;
}

.resume-wrap .text {
    width: calc(100% - 50px);
}

.resume-wrap .date {
    font-weight: 700;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.6);
    color: #3e64ff;
}

.resume-wrap h2 {
    padding-left: 20px;
    font-size: 24px;
    font-weight: 700;
}

.resume-wrap .position {
    padding-left: 20px;
    font-size: 18px;
    font-weight: 700;
    color: #000000;
}